import { h } from 'preact';
import { Link } from 'preact-router/match';
import { url } from '../../utils/date';
import LinkPanel from '../../components/LinkPanel';

const today = url();
const onboarded = !!localStorage.getItem('MindPalace_onboarded');

const Home = () =>
  onboarded ? (
    <div class="wrap wrap--thin center home lift-children">
        <a href="/" class=""><svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="250" height="250" viewBox="0 0 24 24"><title>Mind Palace</title><path d="M 7 2 C 5.3550302 2 4 3.3550302 4 5 L 4 19 C 4 20.64497 5.3550302 22 7 22 L 20 22 L 20 20 L 7 20 C 6.4349698 20 6 19.56503 6 19 C 6 18.43497 6.4349698 18 7 18 L 20 18 L 20 16 L 20 2 L 16 2 L 10 2 L 7 2 z M 7 4 L 10 4 L 10 12.123047 L 11.574219 11.019531 L 13 10.017578 L 16 12.123047 L 16 4 L 18 4 L 18 16 L 7 16 C 6.6481575 16 6.3148915 16.07394 6 16.1875 L 6 5 C 6 4.4349698 6.4349698 4 7 4 z M 12 4 L 14 4 L 14 8.2773438 L 13 7.5761719 L 12 8.2773438 L 12 4 z" fill="#323237"></path></svg></a>
      <Link href={today}>
        <h1>
          Take it sleazy!
        </h1>
        <br></br>
        <span class="button"><Link href={today}>start writing</Link>!</span>
      </Link>

      <aside class="link-panels">
        <LinkPanel
          href="/stats/"
          title="Statistics &amp; Reports"
          variation="f"
        >
          <svg width="30" height="28" xmlns="http://www.w3.org/2000/svg">
            <path
              d="M19 21.6L12.3.9a1.4 1.4 0 00-2.6 0L5.8 12.3H1.4a1.4 1.4 0 100 2.7h5.4c.6 0 1.1-.4 1.3-1L11 5.8l6.9 20.6a1.4 1.4 0 002.6 0L24.2 15h4.4a1.4 1.4 0 100-2.7h-5.4c-.6 0-1.1.3-1.3 1L19 21.5z"
              fill="#F2F3F8"
              fill-rule="nonzero"
            />
          </svg>
        </LinkPanel>
        <LinkPanel href="/settings#data" title="Backup  &amp; Import" variation="s">
          <svg width="30" height="30" xmlns="http://www.w3.org/2000/svg">
            <path
              d="M28.5 18c.8 0 1.5.7 1.5 1.5v6c0 2.5-2 4.5-4.5 4.5h-21C2 30 0 28 0 25.5v-6a1.5 1.5 0 013 0v6c0 .8.7 1.5 1.5 1.5h21c.8 0 1.5-.7 1.5-1.5v-6c0-.8.7-1.5 1.5-1.5zM15 0c.8 0 1.5.7 1.5 1.5v14.4l5-5c.5-.5 1.4-.5 2 0 .6.6.6 1.6 0 2.2l-7.4 7.5a1.5 1.5 0 01-.9.4H15a1.5 1.5 0 01-1-.4L6.3 13a1.5 1.5 0 012.2-2.2l4.9 5V1.5c0-.8.6-1.4 1.4-1.5z"
              fill="#F2F3F8"
              fill-rule="nonzero"
            />
          </svg>
        </LinkPanel>

      </aside>
    </div>
  ) : (
    <div class="wrap wrap--thin center home lift-children">
      <Link href="/get-started/">
        <h1>Welcome to MindPalace</h1>
        <img src="/assets/logo.svg" class="home-image" alt="" />
        <br></br>
        <span class="button">Get started</span>
      </Link>
      <div className="mt20 mb20">
        <h2>What's MindPalace?</h2>
        <p>MindPalace is a private, offline-first personal journal.</p>
        <p>
          Your notes are <strong>only</strong> stored on your device, they're
          never even sent to a server. You don't even need to sign-in to use it!
          It works offline and has RTL support, so you can reflect upon your day on the slow train
          journey home.
        </p>
        <p>
          As well as traditional written entries, you can use MindPalace to
          track sleep, medicines, anxiety, exercise, habits, or just about
          anything else you fancy.
        </p>
        <p>
          It's quick and You can
          even add it to your homescreen as an app.
        </p>
      </div>
    </div>
  );

export default Home;



